import posts from './menu.json';
import menuItem from './templates/post-feed-item.hbs';
import './styles.css';

const refs = {
  fullMenu: document.querySelector('.js-menu'),
  body: document.querySelector('body'),
  newTheme: document.querySelector('.js-switch-input'),
};

buildMenu(posts);
function buildMenu(posts) {
  const markup = posts.map(post => menuItem(post)).join('');

  refs.fullMenu.insertAdjacentHTML('beforeend', markup);
}

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const themeList = localStorage.getItem('theme');
if (themeList === Theme.DARK) {
  refs.newTheme.checked = true;
  refs.body.classList.add(Theme.DARK);
}
function changeInput(event) {
  if (event.target.checked) {
    refs.body.classList.add(Theme.DARK);
    refs.body.classList.remove(Theme.LIGHT);
    localStorage.setItem('theme', Theme.DARK);
  }
  if (!event.target.checked) {
    refs.body.classList.add(Theme.LIGHT);
    refs.body.classList.remove(Theme.DARK);
    localStorage.setItem('theme', Theme.LIGHT);
  }
}
refs.newTheme.addEventListener('change', changeInput);
